.feedbackBlock{
    color: white;
    display: flex;
    flex-direction: column;
    gap: 20px;
    text-align: center;
    border: 1px solid #000;
    width: 400px;
    height: 400px;
    border-radius: 20px;
    margin: 80px auto;
    padding: 70px 40px;
    box-sizing: border-box;
    background-color: rgb(56, 172, 255);
}

.feedbackBlock h1{
    font-size: 20px;
    margin-bottom: 15px;
}
.feedbackBlock p{
   text-align: left;
}

.btnBlock{
    display: flex;
    width: 100%;
    justify-content: space-between;

}

.feedbackBTN{
    padding: 8px;
    border-radius: 20px;
}

.feedbackBTN:nth-child(1){
    background-color: rgb(4, 227, 0);
}
.feedbackBTN:nth-child(2){
    background-color: rgb(255, 248, 56);
}
.feedbackBTN:nth-child(3){
    background-color: rgb(255, 61, 61);
}